}
.BtnBox {
  /* background-color: orangered; */
  width: 200px;
  height: 50px;
  display: flex;
  align-items: center;
  animation: Opactiy 2.8s;
  padding-left: 10px;
}

@keyframes Opactiy {
  0% {
    opacity: 0%;
  }
  10% {
    opacity: 0%;
    transform: translateY(20px);
  }
  50% {
    opacity: 50%;
    transform: translateY(-5px);
  }
  100% {
    opacity: 100%;
    transform: translateY(0);
  }
}

