ul {
  /* background-color: indianred; */
  width: 50%;
  height: 100%;
  display: flex;
  text-decoration: none;
}

ul > li {
  /* background-color: indigo; */
  width: 35%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
ul li a {
  position: relative;
  display: block;
  text-decoration: none;
  font-family: "Lato";

  color: #fff;
  text-transform: uppercase;
  padding: 4px 10px;
  transition: 0.5s;
  /* background-color: red; */
}

ul li a::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 3px;
  top: 90%;
  left: 0;
  background: #ff7000;
  transition: transform 0.5s;
  transform: scaleX(0);
  transform-origin: right;
}
ul li a:hover {
  color: #ff7000;
}
ul li a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}

